import React, { Component } from 'react';

import './searchpanel.css';

export class SearchPanel extends Component {

    state = {
        show: false,
        search: {
            type: 'date',
            operator: 'equally',
            value: ''
        }
    }

    onChangeInput = (event) => {
        const data = {...this.state.search, value: event.target.value}
        this.changeState(data)
    }

    onChangeSelectType = (event) => {
        const data = {...this.state.search, type: event.target.value}
        this.changeState(data)
    }

    onChangeSelectOperator = (event) => {
        const data = {...this.state.search, operator: event.target.value}
        this.changeState(data)
    }

    changeState = data => this.setState({search: data})

    render() {

        const {show, search} = this.state;

        return (
            <div className="search">
                <div className="search__svg" onClick={() => this.setState((state) => ({show: !state.show}))}>
                    <svg 
                        id="Capa_1" 
                        enableBackground="new 0 0 512 512" height="100%" 
                        viewBox="0 0 512 512" 
                        width="20" 
                        xmlns="http://www.w3.org/2000/svg">
                        <g><g>
                        <   path d="m173.101 271.528c3.264 0 6.268-2.148 7.209-5.442l40.411-141.442c1.139-3.983-1.168-8.135-5.151-9.274-3.982-1.138-8.136 1.168-9.274 5.152l-40.411 141.442c-1.139 3.983 1.168 8.135 5.151 9.273.689.197 1.382.291 2.065.291z"/><path d="m243.608 249.23c1.454 1.397 3.326 2.09 5.194 2.09 1.971 0 3.939-.773 5.412-2.306l48.495-50.515c2.787-2.903 2.787-7.488 0-10.391l-48.495-50.514c-2.869-2.989-7.619-3.084-10.607-.216-2.988 2.869-3.086 7.618-.216 10.606l43.507 45.319-43.507 45.32c-2.869 2.989-2.771 7.738.217 10.607z"/><path d="m143 137.378c-2.989-2.87-7.738-2.771-10.606.216l-48.495 50.514c-2.787 2.903-2.787 7.488 0 10.391l48.495 50.515c1.473 1.534 3.441 2.306 5.412 2.306 1.869 0 3.74-.693 5.194-2.09 2.988-2.869 3.086-7.618.216-10.606l-43.507-45.32 43.507-45.319c2.869-2.99 2.772-7.738-.216-10.607z"/><path d="m509.804 433.472-72.404-72.404c-2.93-2.928-7.679-2.928-10.609 0l-7.839 7.839-28.821-28.821 7.84-7.84c2.929-2.93 2.929-7.679 0-10.609l-35.177-35.178c7.207-13.09 12.908-26.994 16.902-41.436 8.582-31.031 9.286-64.026 2.038-95.418-.933-4.036-4.962-6.554-8.997-5.621-4.037.932-6.553 4.96-5.621 8.997 13.954 60.438-3.865 122.671-47.664 166.47-34.785 34.78-80.461 52.168-126.15 52.164-45.678-.004-91.37-17.392-126.141-52.163-69.552-69.562-69.552-182.739 0-252.291 69.552-69.552 182.73-69.552 252.29 0 15.704 15.703 28.169 33.86 37.051 53.966 1.674 3.79 6.105 5.505 9.893 3.831 3.79-1.674 5.505-6.103 3.831-9.892-9.633-21.808-23.146-41.494-40.166-58.514-75.41-75.4-198.104-75.402-273.508 0-75.401 75.402-75.401 198.097 0 273.508 37.705 37.704 87.22 56.556 136.75 56.551 32.126-.003 64.256-7.948 93.16-23.815l35.175 35.175c1.465 1.464 3.385 2.197 5.305 2.197s3.84-.733 5.305-2.197l7.84-7.84 28.821 28.821-7.839 7.839c-2.929 2.93-2.929 7.679 0 10.609l21.624 21.624c1.465 1.464 3.385 2.197 5.305 2.197s3.84-.733 5.305-2.197c2.929-2.93 2.929-7.679 0-10.609l-16.32-16.319 55.114-55.114 67.099 67.1c2.93 2.928 7.679 2.928 10.609 0 2.928-2.931 2.928-7.68-.001-10.61zm-182.862-51.415-27.233-27.233c10.734-7.074 20.914-15.329 30.351-24.764 9.29-9.29 17.563-19.486 24.743-30.372l27.254 27.254zm23.754-2.536 28.825-28.825 28.821 28.821-28.825 28.825z"/><path d="m418.045 473.16c-2.928-2.928-7.678-2.929-10.608.001-2.929 2.929-2.929 7.679.001 10.608l26.035 26.034c1.464 1.464 3.384 2.197 5.304 2.197s3.84-.733 5.305-2.198c2.929-2.929 2.929-7.679-.001-10.608z"/><path d="m60.048 163.556c-4.088-.676-7.949 2.089-8.625 6.177-7.563 45.716 7.459 92.546 40.183 125.27 27.161 27.166 63.277 42.128 101.694 42.128 38.416 0 74.535-14.961 101.702-42.127 27.166-27.166 42.127-63.285 42.127-101.702s-14.962-74.533-42.127-101.694c-27.166-27.167-63.284-42.128-101.701-42.128s-74.533 14.962-101.691 42.125c-12.483 12.473-22.429 26.887-29.561 42.843-1.691 3.782.005 8.218 3.787 9.909 3.778 1.689 8.219-.005 9.909-3.787 6.382-14.276 15.287-27.18 26.472-38.355 24.328-24.333 56.675-37.733 91.085-37.733s66.76 13.401 91.093 37.734c24.333 24.328 37.733 56.676 37.733 91.084 0 34.41-13.401 66.76-37.734 91.092s-56.683 37.734-91.092 37.734-66.757-13.4-91.085-37.734c-29.311-29.31-42.766-71.259-35.991-112.213.675-4.085-2.09-7.947-6.178-8.623z"/>
                        </g></g>
                    </svg>
                </div>
                <ul 
                    className="search__box"
                    style={show ? {opacity: '1'} : {opacity: '0'}} >
                    <li className="search__item">
                        <select 
                            className="search__select" 
                            onChange={(e) => this.onChangeSelectType(e)}>
                                <option value="date">Дата</option>
                                <option value="name">Название</option>
                                <option value="count">Количество</option>
                                <option value="distance">Расстояние</option>
                        </select>
                    </li>
                    <li className="search__item">
                        <select 
                            className="search__select"
                            onChange={(e) => this.onChangeSelectOperator(e)}>
                            <option value="equally">Равно</option>
                            <option value="contains">Содержать</option>
                            {!(search.type === 'name') && <option value="more">Больше</option>}
                            {!(search.type === 'name') && <option value="less">Меньше</option>}
                        </select>
                    </li>
                    <li className="search__item">
                        <input 
                            className="search__input" 
                            type="text" 
                            placeholder="Значение"
                            onChange={(e) => this.onChangeInput(e)} />
                    </li> 
                    <li className="search__item">
                        <button 
                            className="search__button"
                            onClick={() => this.props.searchData(search)}>
                            Поиск
                        </button>
                    </li>
                </ul>
            </div> 
        )
    }
}

export default SearchPanel
